.modalFullscreen {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  display: none; }

.modalFullscreenInner {
  width: 100%;
  height: 100%; }

.modalClose {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 99; }



