:global(.nav) {
  border-bottom: 0; }

:global(.nav-link) {
  border-top: 0;
  font-size: 1em;
  line-height: 1em;
  padding: 0.7rem 1rem; }
  :global(.nav-link):hover {
    background-color: #363636;
    border-color: transparent;
    -webkit-transition: all .25s ease;
    -o-transition: all .25s ease;
    transition: all .25s ease; }

:global(.dropdown-menu) {
  position: static;
  border: 0;
  width: 100%;
  padding: 0;
  margin: 0;
  border-radius: 0; }



